.A_Container {
  height: auto;
  background-color: #f5f5f5;
}
.A_Wrapper {
  padding: 4rem 0rem;
  display: flex;
  gap: 10px;
  justify-content: space-evenly;
  margin: 0px 120px;
}
.A_Text_Wrap {
  flex: 1;
  display: flex;
  gap: 20px;
  flex-direction: column;
  justify-content: space-between;
}
.A_Image_Wrap {
  flex: 1;
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.A_Text_Header {
  font-weight: bold;
  font-size: 40px;
  line-height: 45px;
}
.A_Btn {
  border: 2px solid #f96507;
  background-color: transparent;
  padding: 0.5rem 2rem;
  color: #f96507;
  font-weight: 500;
  font-size: 16px;
  width: fit-content;
  text-decoration: none;
}
.A_Btn:hover {
  color: white;
  transition: all 0.3s linear;
  background-color: #f96507;
}
.A_Text_Title {
  color: #000015;
  line-height: 23.48px;
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 1.5px;
}
.A_Orange {
  color: #f96507;
}
.A_Blue {
  color: #000098;
}
.A_Text_Wrap p {
  text-align: justify;
  line-height: 22px;
}
@media screen and (min-width: 769px) and (max-width: 1199px) {
  .A_Wrapper {
    flex-direction: row;
    padding: 2rem 1rem;
    margin: 0;
  }
  .A_Image_Wrap img {
    width: 48%;
  }
  .A_Text_Wrap p {
    line-height: 22px;
    font-size: 1rem;
  }
  .A_Text_Header {
    line-height: 32px;
    font-size: 2.2rem;
  }
}
@media screen and (max-width: 768px) {
  .A_Wrapper {
    flex-direction: column;
    padding: 2rem 1rem;
    margin: 0;
  }
  .A_Container {
    height: auto;
  }
  .A_Text_Wrap {
    margin-bottom: 3rem;
  }
  .A_Image_Wrap {
    justify-content: center;
  }
  .A_Image_Wrap img {
    width: 48%;
  }
  .A_Text_Title {
    font-size: 14px;
  }
  .A_Text_Header {
    line-height: 1.5;
    font-size: 24px;
  }
  .A_Text_Wrap p {
    line-height: 22px;
    font-size: 1rem;
  }
}
